@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300&display=swap');
button {
    border-radius: 5px;
    border: none;
    /* background-image: linear-gradient( rgb(0 0 0 / 80%), rgb(32, 226, 145)); */
    color: #fff;
    padding: 12px;
    background: #474E68;
}

button:hover {
    transition: 200ms;
    transform: scale(1.1);
}

button:focus {
    outline: none;
}

nav {
    color: rgb(255 255 255);
    /* background-attachment: fixed; */
    /* background-color: rgba(104, 104, 104, 0.747); */
    background-color: #6B728E;
}

.loader {
    margin: 0 auto;
}

.avatar {
    border-radius: 70px;
    /* margin-bottom: 10px; */
    margin-bottom: 2rem;
}

#main {
    padding: 15px 50px;
    margin: 0 auto;
    display: none;
    background: #474E68;
}

#main img {
    animation-delay: 5s;
}

.profile {
    max-width: 300px;
    margin: 15px auto;
    text-align: center;
    font-family: arial;
    padding: 20px;
    border-color: rgba(32, 17, 238, 0.87);
    border-radius: 20px;
    background: #6B728E;
    /* background: linear-gradient(45deg, #ec11bc, #3cb7e7); */
    /* box-shadow:  5px 5px 5px #3cb7e7,
             -5px -5px 5px #ec11bc; */
}

.profile:hover {
    transition: 500ms;
    box-shadow: 20px 20px 50px rgba(0, 0, 0, 0.6);
}

.name {
    font-family: Poppins;
    /* font-style: italic; */
    font-feature-settings: "smcp";
    font-variant: small-caps;
    color: rgb(255 255 255);
    font-size: 25px;
    font-weight: bold;
}

.email {
    /* background-color: #4b4b4b70; */
    border-radius: 40px;
    /* font-family: cursive; */
    font-size: 18px;
    overflow-wrap: break-word;
}